@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
body {
  min-width: 320px;
  margin: 0;
  background-color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.additional {
  margin: 0;
  padding: 0 0 34px;
}

.additional__title {
  margin: 0 0 34px;
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .additional__title {
    margin: 0 0 57px;
    font-size: 36px;
    line-height: 36px;
  }
}

.additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 13px;
}
@media (min-width: 768px) {
  .additional__list {
    grid-template-columns: 1fr 1fr;
    gap: 60px;
  }
}
@media (min-width: 1440px) {
  .additional__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 80px;
  }
}

.additions-card {
  padding: 12px 0 19px;
  border-bottom: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .additions-card:last-child {
    border-bottom: none;
  }
}
.additions-card:first-child {
  border-top: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .additions-card:first-child {
    border-top: none;
  }
}
@media (min-width: 768px) {
  .additions-card {
    padding: 25px 38px 27px;
  }
}
@media (min-width: 1440px) {
  .additions-card {
    padding: 15px 0 16px;
    display: grid;
    grid-template-columns: 1fr 244px;
    gap: 40px;
  }
}

@media (min-width: 768px) {
  .additions-card__info {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .additions-card__info {
    display: grid;
    grid-template-columns: 325px 1fr;
    align-items: center;
  }
}

.additions-card__title {
  margin: 0 0 13px;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .additions-card__title {
    margin: 0 0 25px;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .additions-card__title {
    margin: 0;
  }
}

.additions-card__details-wrapper {
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .additions-card__details-wrapper {
    margin: 3px 0 0;
    gap: 55px;
  }
}
@media (min-width: 1440px) {
  .additions-card__details-wrapper {
    margin-top: 5px;
    gap: 0;
  }
}

.additions-card__details-item {
  font-family: "Lato";
  text-align: center;
}
@media (min-width: 1440px) {
  .additions-card__details-item:first-child {
    width: 235px;
    text-align: start;
  }
}
@media (min-width: 768px) {
  .additions-card__details-item {
    font-size: 16px;
    line-height: 20px;
  }
}

@media (min-width: 1440px) {
  .additions-card__button {
    width: 245px;
  }
}

.additions-container {
  padding: 25px 0 0;
  position: relative;
}
@media (max-width: 767px) {
  .additions-container::before {
    content: "";
    position: absolute;
    background-color: #ebebeb;
    height: 1px;
    left: calc(50% - 50vw);
    top: 0;
    width: 100vw;
  }
}
@media (min-width: 768px) {
  .additions-container {
    padding: 70px 0 0;
  }
}
@media (min-width: 1440px) {
  .additions-container {
    padding: 76px 0 87px;
  }
}

@media (min-width: 1440px) {
  .additions-container__wrapper {
    display: grid;
    grid-template-columns: 1fr 245px;
    gap: 80px;
  }
}

.additions-container__title {
  margin: 0 0 36px 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  position: relative;
}
.additions-container__title::after {
  content: "";
  background-color: #68b738;
  height: 2px;
  position: absolute;
  top: 50%;
}
@media (min-width: 768px) {
  .additions-container__title {
    margin: 0 0 80px 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .additions-container__title {
    margin: 0 0 80px 0;
  }
}

.additions-container__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .additions-container__list {
    border: 1px solid #cdcdcd;
  }
}
@media (min-width: 1440px) {
  .additions-container__list {
    border-left: none;
    border-right: none;
  }
}

.advantages {
  padding-bottom: 50px;
  position: relative;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .advantages::after {
    content: "";
    position: absolute;
    background-color: #e6e6e6;
    height: 1px;
    left: calc(50% - 50vw);
    bottom: 0;
    width: 100vw;
  }
}
@media (min-width: 768px) {
  .advantages {
    padding-bottom: 40px;
  }
}
@media (min-width: 1440px) {
  .advantages {
    padding-bottom: 60px;
  }
}

.advantages__title {
  margin: 0 0 40px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 20px;
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: 325px 1fr;
    column-gap: 60px;
    row-gap: 37px;
    counter-reset: li;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 75px;
  }
}

.advantages__item {
  display: flex;
  align-items: center;
  min-height: 60px;
  background-repeat: no-repeat;
  position: relative;
}
@media (min-width: 768px) {
  .advantages__item {
    display: block;
    min-height: 280px;
    background-position-y: 33px;
    counter-increment: li;
  }
  .advantages__item::after {
    content: counter(li);
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    top: -7px;
    left: 115px;
    z-index: -1;
  }
}
.advantages__item--material {
  background-image: url("../img/index-advantages/advantages-material.svg");
}
@media (min-width: 768px) {
  .advantages__item--material {
    background-image: url("../img/index-advantages/advantages-material-tablet.svg");
  }
}
.advantages__item--form {
  background-image: url("../img/index-advantages/advantages-form.svg");
}
@media (min-width: 768px) {
  .advantages__item--form {
    background-image: url("../img/index-advantages/advantages-form-tablet.svg");
  }
}
.advantages__item--order {
  background-image: url("../img/index-advantages/advantages-order.svg");
}
@media (min-width: 768px) {
  .advantages__item--order {
    background-image: url("../img/index-advantages/advantages-order-tablet.svg");
  }
}
.advantages__item--results {
  background-image: url("../img/index-advantages/advantages-results.svg");
}
@media (min-width: 768px) {
  .advantages__item--results {
    background-image: url("../img/index-advantages/advantages-results-tablet.svg");
  }
}

.advantages__item-description {
  font-family: "Lato", sans-serif;
  margin: 0 0 0 80px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .advantages__item-description {
    margin: 144px 80px 0 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .advantages__item-description {
    margin: 144px 7px 0 0;
    font-size: 16px;
  }
}

.button-common {
  width: 100%;
  font-family: "Oswald";
  padding: 10px 0;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #68b738;
  border: none;
  display: inline-block;
}
.button-common:hover {
  background-color: #5eaa2f;
}
.button-common:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button-common--size-medium {
    padding: 14px 0;
    font-size: 20px;
    line-height: 26px;
  }
}
.button-common--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button-common--secondary:hover {
  background-color: #ebebeb;
}
.button-common--secondary:active {
  background-color: #ebebeb;
  color: #444444;
}

.card-more {
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}
@media (max-width: 767px) {
  .card-more::before {
    content: "";
    position: absolute;
    background-color: #ebebeb;
    height: 1px;
    left: calc(50% - 50vw);
    top: 0;
    width: 100vw;
  }
}
@media (min-width: 768px) {
  .card-more {
    margin-top: 82px;
    padding: 0 37px 40px;
    border: 2px solid #f2f2f2;
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .card-more {
    margin-top: 80px;
    padding: 0 38px 40px;
  }
}

.card-more__info {
  position: relative;
}
.card-more__info::before {
  content: "";
  position: absolute;
  background-image: url("../img/plus.svg");
  width: 30px;
  height: 30px;
  top: 0;
  left: 45%;
}
@media (min-width: 768px) {
  .card-more__info::before {
    background-image: url("../img/plus-big.svg");
    width: 60px;
    height: 60px;
    top: 47px;
    left: 33%;
  }
}
@media (min-width: 1440px) {
  .card-more__info::before {
    top: -100px;
  }
}

.card-more__title {
  margin: 53px 0 8px;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .card-more__title {
    width: 134px;
    margin: 156px auto 23px;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .card-more__title {
    margin: 0 auto 26px;
  }
}

.card-more__subtitle {
  margin: 0 0 21px;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  text-align: center;
}
@media (min-width: 768px) {
  .card-more__subtitle {
    width: 170px;
    margin: 0 0 82px;
    font-size: 16px;
    line-height: 18px;
  }
}
@media (min-width: 1440px) {
  .card-more__subtitle {
    width: 100%;
    margin: 0 0 88px;
  }
}

.catalog-container {
  padding: 0;
}

.catalog-wrapper {
  padding: 27px 0 0;
  position: relative;
}
@media (max-width: 767px) {
  .catalog-wrapper::before {
    content: "";
    position: absolute;
    background-color: #d9d9d9;
    height: 1px;
    left: calc(50% - 50vw);
    top: 0;
    width: 100vw;
  }
}
@media (min-width: 768px) {
  .catalog-wrapper {
    padding: 65px 0 0;
  }
}
@media (min-width: 1440px) {
  .catalog-wrapper {
    padding: 74px 0 0;
  }
}

.catalog-wrapper__title {
  margin: 0 0 41px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .catalog-wrapper__title {
    margin: 0 0 84px 0;
    font-size: 60px;
    line-height: 60px;
  }
}

.checkbox {
  padding-left: 45px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox {
    padding-left: 60px;
  }
}

.checkbox__input[type=checkbox]:checked + .checkbox__mark::after {
  position: absolute;
  top: 6px;
  left: 4px;
  width: 18px;
  height: 14px;
  content: url("../img/checkbox-mobile.svg");
}
@media (min-width: 768px) {
  .checkbox__input[type=checkbox]:checked + .checkbox__mark::after {
    top: 10px;
    left: 8px;
    width: 24px;
    height: 18px;
    content: url("../img/checkbox-tablet.svg");
  }
}

.checkbox__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .checkbox__mark {
    width: 40px;
    height: 40px;
  }
}

.checkbox__text {
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
  padding: 6px 0;
}
@media (min-width: 768px) {
  .checkbox__text {
    font-size: 20px;
    line-height: 30px;
  }
}

.comments {
  margin: 0;
  padding: 0 0 23px;
}

.comments__title {
  margin: 0 0 38px;
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .comments__title {
    margin: 0 0 87px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .comments__title {
    margin: 0 0 78px;
  }
}

.compare-slider {
  position: relative;
  overflow: hidden;
  width: 280px;
  height: 256px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .compare-slider {
    width: 560px;
    height: 512px;
  }
}

.compare-slider__picture {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.compare-slider__picture--after {
  z-index: 100;
}
.compare-slider__picture--before {
  width: 50%;
  z-index: 200;
}

.compare-slider__image {
  width: 280px;
  max-width: 280px;
  height: 100%;
  background-color: #eaeaea;
}
@media (min-width: 768px) {
  .compare-slider__image {
    width: 560px;
    max-width: 560px;
    height: 512px;
    background: linear-gradient(#ffffff 151px, #eaeaea 151px);
  }
}
@media (min-width: 1440px) {
  .compare-slider__image {
    background: linear-gradient(#ffffff 135px, #eaeaea 135px);
  }
}

.compare-slider__dragme {
  padding: 0;
  margin: 0;
  position: absolute;
  width: 4px;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #666666;
  cursor: pointer;
  z-index: 300;
  border: none;
  border-radius: 2px;
}
.compare-slider__dragme:hover {
  background-color: #68b738;
}
.compare-slider__dragme:hover .compare-slider__arrow {
  border-color: #68b738;
  color: #68b738;
}
.compare-slider__dragme:active {
  background-color: rgba(94, 170, 47, 0.6);
}
.compare-slider__dragme:active .compare-slider__arrow {
  border-color: rgba(94, 170, 47, 0.6);
  color: rgba(94, 170, 47, 0.6);
}

.compare-slider__arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  text-align: center;
  background-color: #ffffff;
  color: #666666;
  border: 2px solid #666666;
  cursor: pointer;
  z-index: 300;
}

