@use "../utils/variables.scss";

.logo {
  display: flex;
  align-items: center;
  gap: 56px;

  @media (min-width: variables.$tablet-width) {
    gap: 13px;
  }

  @media (min-width: variables.$desktop-width) {
    gap: 14px;
  }

  &:has(a:hover) {
    opacity: 0.8;
  }

  &:has(a:active) {
    opacity: 0.6;
  }
}
